<template>
  <el-container id="app" class="container">
    <div class="canvas-wrap">
      <app-canvas/>
    </div>
    <transition appear v-on:enter="asideEnter" v-on:leave="asideLeave">
      <el-aside class="aside">
        <app-aside/>
      </el-aside>
    </transition>
    <transition appear v-on:enter="containerEnter" v-on:leave="containerLeave">
      <el-container class="main">
        <el-header class="header">
          <app-header/>
        </el-header>
        <el-main>Main</el-main>
        <el-footer>Footer</el-footer>
      </el-container>
    </transition>
  </el-container>
</template>

<script>
import AppCanvas from "./components/public/canvas.vue";
import AppAside from "./components/public/aside.vue";
import AppHeader from "./components/public/header.vue";
import Velocity from "velocity-animate";
export default {
  elem: "#app",
  data() {
    return {
      show: true
    };
  },
  components: {
    AppCanvas,
    AppAside,
    AppHeader
  },
  methods: {
    asideEnter: function(el, done) {
      Velocity(el, "stop");
      Velocity(el, { left: 0 }, { duration: 800, easing: "spring" });
    },
    asideLeave: function(el, done) {
      Velocity(el, "stop");
      Velocity(el, { left: '-300px' }, { duration: 800, easing: "spring" });
    },
    containerEnter(el, done){
      Velocity(el, "stop");
      Velocity(el, { marginLeft: '300px' }, { duration: 800, easing: "spring" });
    },
    containerLeave(el, done){
      Velocity(el, "stop");
      Velocity(el, { marginLeft: '0px' }, { duration: 800, easing: "spring" });
    }
  },
};
</script>

<style lang="scss">
@import "./assets/css/app.scss";
</style>
