<template>
  <div class="header-wrap">
    <div>
      <a href="/"  class="menu-item">首页</a>
      <a href="/"  class="menu-item">首页</a>
    </div>
    <div>
      <a href="javascript:void(0)" class="search title-name">
        <i class="el-icon-search"></i>&nbsp;
        <span>搜索</span>
      </a>
      <a href="javascript:void(0)" class="title-name">
        <span>个人主页</span>
      </a>
      <span id="time" style="margin-left:16px;"></span>
    </div>
  </div>
</template>

<script>
export default {
  mounted() {
    const t = document.getElementById("time");
    let time = new Date();
    let hours = time.getHours();
    let minutes = time.getMinutes();
    let secconds = time.getSeconds();
    t.innerHTML = `${this.formatDate(hours)}:${this.formatDate(
      minutes
    )}:${this.formatDate(secconds)}`;
    setInterval(() => {
      time = new Date();
      hours = time.getHours();
      minutes = time.getMinutes();
      secconds = time.getSeconds();
      t.innerHTML = `${this.formatDate(hours)}:${this.formatDate(
        minutes
      )}:${this.formatDate(secconds)}`;
    }, 1000);
  },
  methods: {
    formatDate(num) {
      return num > 9 ? num : "0" + num;
    }
  }
};
</script>

<style lang="scss">
@import "../../assets/css/public/header.scss";
</style>

